/* ============================================= */
/*              Navigation                       */
/* ============================================= */

.nav {
  padding-left: 0;
  flex-basis: 100%;
  @include flex-col;
  &-link {
    text-align: center;
    border-radius: 5px;
    padding: 10px 0;
    display: block;
    width: 100%;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    &:hover {
      color: $accent-color;
      background-color: $secondary-color;
    }
  }
}
